
.TeamPage {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
    column-gap: 10px;
    row-gap: 10px;
    


    .team-name-section {
        grid-column: span 3;
        justify-self: flex-start;
        align-self: flex-end;
        
    }
    
    .team-name {
        font-size: 4.5rem;
        margin-bottom: 20px;
    }

    .win-loss-section {
        justify-self: center;
        padding: 40px;
        text-align: center;
    }
    .match-detail-section {
        grid-column: span 4;
    }

    .more-link {
        align-self: center;
        justify-self: center;
        font-size: 1.5rem;

        & > a {
            text-decoration: none;
        }
    }
}
